@import "global";
@import "text";

body {
  cursor: pointer;

  div {
  position: absolute;
    width: 100%;
    text-align: center;
}
}

#intro-logo {
  width: 100%;
  top: 50%;
  transform: translate(0, -110%);
  text-align: center;

  img {
    width: 22vw;
    padding-bottom: 1rem;
  }

  div {
    font-size: 1.8vw;
  }
}

#intro-advice {
  bottom: 10vh;
	-webkit-animation: flash ease 1s infinite;
	animation: flash ease 1s infinite;
}
@-webkit-keyframes flash {
	0% { opacity: 1; }
	50% { opacity: .1; }
	100% { opacity: 1; }
}
@keyframes flash {
	0% { opacity: 1; }
	50% { opacity: .1; }
	100% { opacity: 1; }
}
